import React from 'react';
import { navigate } from 'gatsby';
import Img from 'gatsby-image';

export default function BlockCase({ title, subtitle, image, link }) {
  function handleNavigateTo() {
    navigate(link);
  }

  return (
    <div
      role="button"
      tabIndex={0}
      onClick={handleNavigateTo}
      onKeyDown={handleNavigateTo}
      className="block-case"
    >
      <Img
        fluid={image}
        imgStyle={{ objectFit: 'contain' }}
        alt={title}
        className="block-case__image"
      />
      <div className="block-case__subtitle">{subtitle}</div>
      <div className="block-case__title">{title}</div>

      <style jsx>{`
        .block-case {
          cursor: pointer;
          transition-duration: 300ms;
        }
        .block-case__title {
          font-size: 20px;
        }
        .block-case__subtitle {
          padding: 10px 0;
        }
        .block-case:hover {
          transform: translateY(-3px);
        }
      `}</style>
      <style jsx global>{`
        .block-case__image {
        }
      `}</style>
    </div>
  );
}
